.header {
  position: relative;

  &::before{
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(var(--bg-b),1.0);
    height: 1px;
  }


  &__logo{

    @include small-tablet{
      svg{
      width: 134px;
      height: auto;
    }
  }
  }
}
