footer {
  padding: clamp(50px, 6.604vw, 84px) 0;
  position: relative;

  &::before{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba(var(--bg-b),1.0);
    height: 1px;
  }
}

.footer{
  &__logo{
    @include small-tablet{
        width: 80px;
        height: auto;

        order: 3;
    }

  }
}

